import React from "react";
import "./IncomeItem.css";
import CostDate from "./CostDate";
import Card from "./Card";

function IncomeItem(props) {
  const deleteHandler = () => {
    props.onDelete(props.id);
  };

  const editHandler = () => {
    props.onEdit(props.id);
    window.scrollTo({
      top: 100,
      behavior: "smooth",
    });
  };

  return (
    <li>
      <Card className="cost-item">
        <div className="cost-item__left">
          <CostDate date={props.date} />
          <div className="cost-item__description">
            <h3>{props.type}</h3>
          </div>
        </div>
        <div className="cost-item__right">
          <div className="cost-income-item__price">${props.amount}</div>
          {/*  entrytype */}
          <span className="cost-income-item__type">{props.entrytype}</span>
          <div className="cost-item__actions">
            <button className="cost-item__edit" onClick={editHandler}>
              
            </button>
            <button className="cost-item__delete" onClick={deleteHandler}>
              
            </button>
          </div>
        </div>
      </Card>
    </li>
  );
}

export default IncomeItem;