import Diagram from './Diagramm/Diagram';

const CostDiagram = (props) => {
  const diagramDataSet = [
    { label: 'Jan', value: 0, income: 0 },
    { label: 'Feb', value: 0, income: 0 },
    { label: 'Mar', value: 0, income: 0 },
    { label: 'Apr', value: 0, income: 0 },
    { label: 'May', value: 0, income: 0 },
    { label: 'Jun', value: 0, income: 0 },
    { label: 'Jul', value: 0, income: 0 },
    { label: 'Aug', value: 0, income: 0 },
    { label: 'Sep', value: 0, income: 0 },
    { label: 'Oct', value: 0, income: 0 },
    { label: 'Nov', value: 0, income: 0 },
    { label: 'Dec', value: 0, income: 0 }
  ];

  for (const cost of props.costs) {
    const costMonth = cost.date.getMonth(); //  (0-11)
    diagramDataSet[costMonth].value += cost.price;
  }

  for (const income of props.incomes) {
    const incomeMonth = new Date(income.date).getMonth();
    diagramDataSet[incomeMonth].income += income.amount;
    diagramDataSet[incomeMonth].entrytype = income.entrytype;
  }

  return <Diagram dataSet={diagramDataSet} />;
};

export default CostDiagram;
