import { useState } from "react";
import { supabase } from "../supabaseClient";
import "./Auth.css";

const Auth = () => {
  const [isOtpSent, setIsOtpSent] = useState(false);
  const [otpMode, setOtpMode] = useState(false); //    OTP   
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [otp, setOtp] = useState("");
  const [errorMessage, setErrorMessage] = useState(""); //   
  const [emailError, setEmailError] = useState(false); //   email

  const handleSignUpWithOTP = async () => {
    try {
      setErrorMessage(""); //  
      setEmailError(false);

      const { error } = await supabase.auth.signUp({
        email,
        password,
      });

      if (error) throw error;

      setIsOtpSent(true);
    } catch (error) {
      setErrorMessage(error.message);
    }
  };

  const handleVerifyOTP = async () => {
    try {
      setErrorMessage(""); //  
      setEmailError(false);

      const { error } = await supabase.auth.verifyOtp({
        email,
        token: otp,
        type: "signup",
      });

      if (error) throw error;

      setIsOtpSent(false);
    } catch (error) {
      setErrorMessage(error.message);
    }
  };

  const handleSignIn = async () => {
    try {
      setErrorMessage(""); //  
      setEmailError(false);

      const { error } = await supabase.auth.signInWithPassword({
        email,
        password,
      });

      if (error) throw error;
    } catch (error) {
      setErrorMessage("Invalid login or password");
      setEmailError(true);
    }
  };

  const resetError = () => {
    setErrorMessage("");
    setEmailError(false);
  };

  return (
    <div className="auth-container">
      <h2 className="auth-heading">Welcome</h2>
      <div className="auth-form">
        <input
          type="email"
          placeholder="Email"
          value={email}
          onChange={(e) => {
            setEmail(e.target.value);
            resetError(); //     email
          }}
          className={`auth-input ${emailError ? "error" : ""}`} //    
        />
        {!otpMode && (
          <input
            type="password"
            placeholder="Password"
            value={password}
            onChange={(e) => {
              setPassword(e.target.value);
              resetError(); //     
            }}
            className="auth-input"
          />
        )}

        {isOtpSent && otpMode && (
          <input
            type="text"
            placeholder="Enter OTP"
            value={otp}
            onChange={(e) => {
              setOtp(e.target.value);
              resetError(); //     OTP
            }}
            className="auth-input"
          />
        )}

        {errorMessage && <div className="error-message">{errorMessage}</div>}

        <div className="auth-buttons">
          {!otpMode ? (
            <>
              <button
                onClick={handleSignIn}
                className="auth-button signin"
              >
                Sign In
              </button>
              <button
                onClick={() => {
                  setOtpMode(true);
                  resetError(); //       OTP
                }}
                className="auth-button signup"
              >
                Sign Up with OTP
              </button>
            </>
          ) : isOtpSent ? (
            <button
              onClick={handleVerifyOTP}
              className="auth-button verify-otp"
            >
              Verify OTP
            </button>
          ) : (
            <button
              onClick={handleSignUpWithOTP}
              className="auth-button signup"
            >
              Send OTP
            </button>
          )}
        </div>
        {otpMode && (
          <button
            onClick={() => {
              setOtpMode(false);
              resetError(); //       
            }}
            className="auth-button back"
          >
            Back to Sign In
          </button>
        )}
      </div>
    </div>
  );
};

export default Auth;
