import React from 'react';
import './CostItem.css';
import CostDate from './CostDate';
import Card from './Card';

function CostItem(props) {
  const deleteHandler = () => {
    props.onDelete(props.id);
  };

  const editHandler = () => {
    props.onEdit(props.id);
    window.scrollTo({
      top: 100,
      behavior: "smooth",
    });
  };

  return (
    <li>
      <Card className="cost-item">
        <div className="cost-item__left">
          <CostDate date={props.date} />
          <div className="cost-item__description">
            <h3>{props.description}</h3>
            <span className="cost-item__category">
              {props.category || "No category"}
            </span>
          </div>
        </div>
        <div className="cost-item__right">
          <div className="cost-item__price">${props.price}</div>
          <span className="cost-item__type">{props.entryType}Expense</span> {/*   */}
          <div className="cost-item__actions">
            <button className="cost-item__edit" onClick={editHandler}>
              
            </button>
            <button className="cost-item__delete" onClick={deleteHandler}>
              
            </button>
          </div>
        </div>
      </Card>
    </li>
  );
}

export default CostItem;