import { useState } from "react";
import IncomeForm from "./IncomeForm";
import "./NewIncome.css";

const NewIncome = (props) => {
  const [isFormVisible, setVisible] = useState(false);

  const saveIncomeDataHandler = (incomeData) => {
    const formattedIncome = {
      ...incomeData,
      id: Math.random().toString(), //  ID
    };

    props.onAddIncome(formattedIncome);
    setVisible(false);
  };

  return (
    <div className="new-income">
      {!isFormVisible && <button onClick={() => setVisible(true)}>Add New Income</button>}
      {isFormVisible && (
        <IncomeForm
          onSaveIncomeData={saveIncomeDataHandler}
          onCancel={() => setVisible(false)}
        />
      )}
    </div>
  );
};

export default NewIncome;
